/* global jQuery React ReactDOM FilterableReleaseNotes */

/*
 * fetch and re-render release notes listings with latest data
 */
(function () {

	"use strict";

	const url = window.location.origin + "/documentation/release-notes/index.json";
	fetch(url).then(res => res.json()).then(data => {

		const element = document.getElementById("reactapp");
		ReactDOM.hydrate(
			React.createElement(
				FilterableReleaseNotes,
				{ "releaseNotes": data }
			),
			element
		);

	});

}());

(function ($) {
	"use strict";

	$(document).ready(function () {

		$(document).on("click", ".filter-handle", function () {
			$(this).toggleClass("open");
			$(".filter-form").slideToggle("slow");
		});

		var badges = ['gav','csam','ai','cri','av','vm','vmdr','vmdrot','vmdrmobile','tp','cm','pm','ioc','cra','ci','csa','cs','was','waf','md','pc','pci','sca','fim','saq','oca','api','ca','qgs','pns', 'edr', 'xdr', 'adm', 'saasdr', 'conn', 'car', 'tc','qf'];
		var hashValue = window.location.hash;
			hashValue = ( '' !== hashValue ? hashValue.substring(1) : '' );

		if( '' !== hashValue ) {
			if( $.inArray( hashValue, badges ) >= 0 ) {
				var reactApp = $("#reactapp");
				reactApp.addClass("filter-open");
			}
		}

	});

}(jQuery));
